import {
  Component,
  OnInit,
  Output,
  EventEmitter,
  Input,
  ViewChild
} from "@angular/core";
import { NodeListComponent } from "../node-list/node-list.component";
import { INode } from '../shared/INode';

@Component({
  selector: "app-playground",
  templateUrl: "./playground.component.html",
  styleUrls: ["./playground.component.scss"]
})
export class PlaygroundComponent implements OnInit {
  @ViewChild(NodeListComponent, null) nodeListComponent: NodeListComponent;

  editedNode: INode = {
    text: "",
    title: "",
    childNodes: [],
    x: 0,
    y: 0,
    width: null,
    height: null,
    color: ""
  };
  editorExpandEventHandler(node: INode){
    console.log(node);
    this.editedNode = node;
  }



  cleanPlayground() {
    let node = {
      text: "some text",
      title: "some title",
      childNodes: [],
      x: 0,
      y: 0,
      width: null,
      height: null,
      color: "#FFFFFF"
    }
    this.nodeListComponent.nodeList = [];
    this.nodeListComponent.nodeList.push(node);
    this.editedNode = node;
  }
  constructor() {}

  ngOnInit() {}
}
